import { useNavigate } from "react-router-dom";
// import { useBalance } from "../../redux/store";
import { useEffect, useState } from "react";

export const WalletTransferView = (props) => {
    // const [walletData, setWalletData] = useState({
    //     wallet: 0,
    //     winning_wallet: 0,
    //     bonus: 0,
    //     commission: 0
    // });


    const navigate = useNavigate();
    const navToWithdraw = () => {
        return navigate("Withdraw");
    };

    const navToDeposite = () => {
        return navigate("Deposite");
    };
    const navToDepositeHistory=()=>{
        return navigate("deposite-history");
    }
    const navToWithdrawHistory=()=>{
        return navigate("withdraw-history")
    }

    return (
        <>
            <div className="wallet-transfer-view-container">
                <div className="wallet-percentage">
                    <div className="main-wallet-percentage">
                        <div>{'₹'}{props.winning_wallet}.00</div>
                        <h5>Winning Wallet</h5>
                    </div>
                    <div className="main-wallet-percentage">
                        <div>{'₹'}{props.bonus}.00</div>
                        <h5>Bonus</h5>
                    </div>
                    <div className="main-wallet-percentage">
                        <div>{'₹'}{props.commission}.00</div>
                        <h5>Commission</h5>
                    </div>
                </div>
                <div className="deposite-amount-button" style={{ width: "94%", fontFamily: "inter", fontWeight: "550" }}>Main Wallet transfer</div>
                <div className="items-transfer-container">
                    <div className="items-transfer" onClick={navToDeposite}>
                        <img alt="img" src="https://bdggame.in/assets/png/rechargeIcon-c2eb6382.png" />
                        <div>Deposit</div>
                    </div>
                    <div className="items-transfer" onClick={navToWithdraw}>
                        <img alt="img" src="data:image/png;base64," />
                        <div>Withdraw</div>
                    </div>
                    <div className="items-transfer" onClick={navToDepositeHistory}>
                        <img alt="img" src="https://bdggame.in/assets/png/rechargeHistory-a584338d.png" />
                        <div>Deposit</div>
                        <div>History</div>
                    </div>
                    <div className="items-transfer" onClick={navToWithdrawHistory}>
                        <img alt="img" src="data:image/png;base64," />
                        <div>Withdraw </div>
                        <div>History</div>
                    </div>
                </div>
            </div>
        </>
    );
};
