import { useNavigate } from "react-router-dom";
export const ServiceCenter = () => {
    const Navigate = useNavigate();
  // handle click event of logout button
  const handleLogout =() => {
    // remove the token and user from the session storage
    sessionStorage.removeItem('token');
    sessionStorage.removeItem('user');
    localStorage.removeItem('token');
    Navigate("/AppLoginRegisterPage/login")

  }
    return (<>
        <div className="service-center-container">
            <h5>Service Center</h5>
            <div className="sevice-center-opration-container">
                <div className="service-center-divs">
                    <img alt="service images" src="https://bdggame.in/assets/png/settingCenter-d46ac1a5.png" />
                    <p>Setting</p>
                </div>
                <div className="service-center-divs">
                    <img alt="service images" src="data:image/png;base64," />
                    <p>Feedback</p>
                </div>
                <div className="service-center-divs">
                    <img alt="service images" src="data:image/png;base64," />
                    <p>Notification</p>
                </div>
                <div className="service-center-divs">
                    <img alt="service images" src="https://bdggame.in/assets/png/serviceCenter-b92936b9.png" />
                    <p>24/7 Customer</p>
                    <p>Service</p>
                </div>
                <div className="service-center-divs">
                    <img alt="service images" src="data:image/png;base64," />
                    <p>Beginner's Guide</p>
                </div>
                <div className="service-center-divs">
                    <img alt="service images" src="https://bdggame.in/assets/png/about-38aa0cd6.png" />
                    <p>About us</p>
                </div>
            </div>
        </div>
        <div className="weekly-tasks-five-div logout" style={{color:"#d9ac4f",border:"1px solid #d9ac4f",margin:"2rem auto",background:"transparent"}} onClick={handleLogout}><img alt="logout" src="https://bdggame.in/assets/png/logout-3fc8c261.png" /> Log out</div>
    </>)
}